// Data: Covid vaccinations in Taiwan
export const useData = () => {
  const [data, setData] = useState(null);
  // if (data) console.log(data);
  useEffect(() => {
    let isMounted = true;
    const row = (d) => {
      d.id = +d["ID"];
      d.date = new Date(d[""]);
      d.county = d[""];
      d.population = +d[""].replace(/,/g, "");
      d.lastTotalVaccinated = +d[""].replace(/,/g, "");
      d.newVaccinated = +d[""].replace(/,/g, "");
      d.totalVaccinated = +d[""].replace(/,/g, "");
      d.totalVaccinatedRate = +d[" (%)"].replace(/,|%/g, "");
      d.vaccinesCount = +d[""].replace(/,/g, "");
      d.vaccinesLeftRate = +d[" (%)"].replace(/,|%/g, "");
      d.vaccinesCountRate = (d.vaccinesCount / d.population) * 100;
      return d;
    };
    csv(csvUrl, row).then((d) => {
      if (isMounted) setData(d);
    });
    return () => {
      isMounted = false;
    };
  }, []);
  return data;
};
